* {
    font-family: Helvetica, Arial, sans-serif;
}

body {
    padding: 60px;
    background: linear-gradient(to right, #bdc3c7, #2c3e50);
}

input {
    color: grey;
    font-size: 12px;
    width: 100%;

}

.card-grid {
    display: flex;
    flex-direction: initial;
    flex-wrap: wrap;
}

.card {
    align-content: center;
    border: 1px solid grey;
    border-radius: 6px;
    margin-bottom: 10px;
    margin-right: 10px;
    overflow: initial;
}

.card p {
    padding: 5px;
    text-align: center;
    text-transform: lowercase;
}

.card img {
    max-height: 170px;

}